'use strict';

/**
* Create an "empty" readable stream.
*
* @module @stdlib/streams/node/empty
*
* @example
* import inspectStream from '@stdlib/streams/node/inspect-sink';
* import emptyStream from '@stdlib/streams/node/empty';
*
* function log( chunk ) {
*    console.log( chunk.toString() );
* }
*
* var stream = emptyStream();
*
* stream.pipe( inspectStream( log ) );
*
* @example
* import emptyStream from '@stdlib/streams/node/empty';
*
* var opts = {
*     'objectMode': false
* };
*
* var createStream = emptyStream.factory( opts );
*
* // Create 10 identically configured streams...
* var streams = [];
* var i;
* for ( i = 0; i < 10; i++ ) {
*     streams.push( createStream() );
* }
*
* @example
* import inspectStream from '@stdlib/streams/node/inspect-sink';
* import emptyStream from '@stdlib/streams/node/empty';
*
* function log( v ) {
*    console.log( v );
* }
*
* var stream = emptyStream.objectMode();
*
* stream.pipe( inspectStream.objectMode( log ) );
*/


// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import objectMode from './object_mode.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'objectMode', objectMode );
setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
