'use strict';

// MODULES //

import EmptyStream from './main.js';


// MAIN //

/**
* Returns an "objectMode" empty readable stream.
*
* @returns {EmptyStream} Stream instance
*
* @example
* import inspectStream from '@stdlib/streams/node/inspect-sink';
*
* function log( v ) {
*    console.log( v );
* }
*
* var stream = objectMode();
*
* stream.pipe( inspectStream.objectMode( log ) );
*/
function objectMode() {
	var opts = {
		'objectMode': true
	};
	return new EmptyStream( opts );
}


// EXPORTS //

export default objectMode;
